.flexContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 120px;
}

.inputStyle{
    font-size: 15px;
	padding: 25px;
    border: none;
    width: 100%;
    box-shadow: 1px 1px 10px rgb(189, 189, 189);
    border-radius: 5px;
    display: inline-block;
    outline: none;
}

.buttonStyle{
    width: 150px;
    height: 70px;
    margin-left: 50px;
    border: none;
    border-radius: 5px;
    background-color: #000;
    color: white;
    font-size: 25px;
    cursor: pointer;
}