@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
.flexContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 120px;
}

.inputStyle{
    font-size: 15px;
	padding: 25px;
    border: none;
    width: 100%;
    box-shadow: 1px 1px 10px rgb(189, 189, 189);
    border-radius: 5px;
    display: inline-block;
    outline: none;
}

.buttonStyle{
    width: 150px;
    height: 70px;
    margin-left: 50px;
    border: none;
    border-radius: 5px;
    background-color: #000;
    color: white;
    font-size: 25px;
    cursor: pointer;
}
.image__list{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 5px;
    grid-auto-rows: 10px;
    place-items:center;
}

.image__list img{
    width: 250px;
    object-fit: cover;
    float: left;
}
.wrapper{
    font-family: 'Roboto Condensed';
    background-color: rgb(235, 235, 235);
    margin: 50px 10vw;
    padding: 50px 7vw;
}

.app{

}

.title{
    font-size: 25px;
    font-weight: bold;
}
